import { Component} from '@angular/core';
import { NewCotrustService } from '../../services/newconsole.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import 'rxjs/add/operator/switchMap';

@Component({
  selector: 'uploadprivatekey',
  templateUrl: './uploadprivatekey.component.html',
  styleUrls: ['./uploadprivatekey.component.css']
})


export class PrivateKeyUploadPage {
  networkid: string;
  showLoading = false;
  showError = false;
  showInfo = false;
  errorMessage = "";
  configArray = [];
  newFileName = "";
  newFileContent = "";
  selectedFolder = ""; 

  constructor(private router: Router, private consoleService: NewCotrustService, private route: ActivatedRoute) {
    console.log("PrivateKeyUploadPage :: Constructor Ends ");
  }

  ngOnInit() {
    this.networkid = this.route.snapshot.paramMap.get('networkid');
    console.log("ngOnInit :: SettingsPage :: networkid :: ",this.networkid);
    this.retrievePrivateKeyDetailsForUser()
  }

  retrievePrivateKeyDetailsForUser(){
    this.showLoading = true;
    const userEmail = this.consoleService.loginUserEmail();
    this.consoleService.retrievePrivateKeyDetailsForUser(userEmail)
      .then((response: any) => {
        console.log("Dashboard :: retrievePrivateKeyDetailsForUser :: ",response);
        this.showLoading = false;
        if(response.errorCode == 1){
            console.log("Dashboard :: Valid response :: ",response.msg);
            this.configArray = response.msg;
        } else {
            this.showError = true;
            this.errorMessage = "No Network Available";
        }
      })
      .catch((err) =>{
        this.showLoading = false;
        console.log("retrieveNetworkBasicDetails() :: err ::",err);
        this.showError = true;
        this.errorMessage = "Network Error";
      })
  }

  fileChange(event) {
    let reader = new FileReader();
    if(event.target.files && event.target.files.length > 0) {
      let file = event.target.files[0];
      console.log("file :: ",file)
      reader.readAsDataURL(file);
      reader.onload = () => {
        console.log("reader :: ",reader)
        // console.log("reader Data :: ",reader.result.split(',')[1])
        this.newFileName = file.name;
        this.newFileContent = reader.result.split(',')[1];
      };
    }
  }

  uploadFile() {
    this.showLoading = true;
    this.consoleService.uploadPrivateKey({
      "networkId" : this.networkid,
      "folderName" : this.selectedFolder,
      "filename" : this.newFileName,
      "filecontent" :  this.newFileContent
    })
    .then((response: any)=>{
      this.showError = false;
      if(response && response.errorCode == 0){
        // this.showLoading = false;
        this.showInfo = true;
        this.errorMessage = "File uploaded successfully please wait redirecting to dashboard...";
        setTimeout(() => {
          this.router.navigate(['/dashboard', this.networkid]);
        }, 3000)
      } else {
        this.showLoading = false;
        this.showError = true;
        this.errorMessage = "Error in uploading file.";
        console.log(response);
      }
    })
    .catch((err)=>{
      this.showLoading = false;
      this.showError = true;
      console.log("retrieveNetworkBasicDetails() :: err ::",err);
      this.errorMessage = "Network Error";
    })
  }

}



