import { Component} from '@angular/core';
import { NewCotrustService } from '../../services/newconsole.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import 'rxjs/add/operator/switchMap';

@Component({
  selector: 'apipage',
  templateUrl: './apipage.component.html',
  styleUrls: ['./apipage.component.css']
})


export class APIPage {

  functionName ="";
  args ="";
  result =" -- ";
  networkid

  peerDetailsArray = [];
  orgDetailsArray = [];
  channelsArray = [];
  chaincodeArray = [];

  selectedOrg = "0";
  selectedPeer = "0";
  selectedChannel = "0";
  selectedChaincode = "0";
  selectedQueryType = "query";

  showLoading = false;
  showError = false;
  errorMessage = "";

  constructor(private router: Router, private consoleService: NewCotrustService, private route: ActivatedRoute) {
    
  }



  onChangeOrganization(){
    console.log("onChangeOrganization :: selectedOrg :: ", this.selectedOrg, " :: peerDetailsArray :: ", this.peerDetailsArray);
    this.selectedPeer = "0";
    this.peerDetailsArray = [];

    this.selectedChannel = "0";
    this.channelsArray = [];

    this.selectedChaincode = "0";
    this.chaincodeArray = [];

    for(let org of this.orgDetailsArray){
      if(org.orgIdentifier == this.selectedOrg){
        this.peerDetailsArray = org.peers;
      }
    }
    console.log("onChangeOrganization :: selectedOrg :: ", this.selectedOrg, " :: peerDetailsArray :: ", this.peerDetailsArray);
  }

  onChangePeer(){
    this.channelsArray = [];

    this.selectedChannel = "0";
    this.channelsArray = [];

    this.selectedChaincode = "0";
    this.chaincodeArray = [];

    if(this.selectedPeer == "0"){

    } else {
      
      this.retrieveChannels(this.selectedOrg, this.selectedPeer, this.networkid)  
    }
    
  }

  onChangeChannel(){
    this.selectedChaincode = "0";
    this.chaincodeArray = [];
    if(this.selectedChannel == "0"){

    } else {
      this.selectedChaincode = "0";
      this.retrieveInstantiatedChaincode(this.networkid, this.selectedOrg, this.selectedPeer, this.selectedChannel)  
    }
  }

  retrieveNetworkBasicDetails(networkid){
    this.showLoading = true;
    this.showError = false;
    this.selectedOrg = "0"
    this.consoleService.retrieveNetworkBasicDetails(networkid)
      .then((response: any) => {
        this.showLoading = false;
        this.orgDetailsArray = [];
        this.selectedOrg = "0";
        
        for(let networdetails of response.networkbasicdetail) {
          let peers = [];
          for(let tmpPeer of networdetails.peers) {
            peers.push(tmpPeer);
          }
          this.orgDetailsArray.push({
            orgName : networdetails.orgName,
            orgIdentifier : networdetails.orgIdentifier,
            peers : peers
          })
        }
        
        console.log("APIPage :: selectedOrg :: ",this.selectedOrg, " :: orgDetailsArray :: ", this.orgDetailsArray);

      })
      .catch((err) =>{
        this.showLoading = false;
        console.log("APIPage :: retrieveNetworkBasicDetails() :: err ::",err);
        this.showError = true;
        this.errorMessage = "Network Error";
      })
  }

  retrieveChannels(selectedOrg, peerName, networkid){
    this.showError = false;
    this.showLoading = true;
    this.channelsArray = [];
    this.consoleService.retrieveOrgDashChannelDetails(networkid,selectedOrg,peerName)
    .then((response: any)=>{
      console.log("retrieveChannelCount() :: response ::",response);
      this.showLoading = false;
      if(this.networkid == networkid 
        && this.selectedOrg == selectedOrg
        && this.selectedPeer == peerName){
          this.channelsArray = [];
          for(let channel of response.channelNames){
            this.channelsArray.push(channel.channelId);
          }
        
      }        
    })
    .catch((err) =>{
      console.log("retrieveChannelCount() :: err ::",err);
      this.showLoading = false;
      this.showError = true;
      this.errorMessage = "Network Error";
    })
  }

  retrieveInstantiatedChaincode(networkid, selectedOrg, peerName, channelName){
    this.showLoading = true;
    this.showError = false;
    this.consoleService.retrieveOrgInstantiatedDashChaincodeDetails(networkid,selectedOrg,peerName,channelName)
    .then((response: any)=>{
      this.showLoading = false;
      if(this.networkid == networkid && this.selectedOrg == selectedOrg 
      && this.selectedPeer == peerName && this.selectedChannel == channelName){
        this.chaincodeArray = [];
        if(response.list && response.list.chaincodes){
          for(let chaincode of response.list.chaincodes){
            this.chaincodeArray.push({
              chaincodeName : chaincode.name,
              chaincodeVersion : chaincode.version,
            });
          }
        }
      }
    })
    .catch((err) =>{
      console.log("retrieveInstantiatedChaincode() :: err ::",err);
      this.showLoading = false;
      this.showError = true;
      this.errorMessage = "Network Error";
    })
  }

  convertTextToArray(arrayString){
    try {
      let jsonString = '{ "value" : ['+arrayString+']}';
      console.log("convertTextToArray :: jsonString :: ", jsonString);
      return JSON.parse(jsonString).value;
    } catch(err){
      this.showLoading = false;
      this.showError = true;
      this.errorMessage = "Invalid Arguments";
    }
  }

  validateInputs(){
    this.showError = false;
    this.errorMessage = "";
    if(this.selectedOrg == "0"){
      this.showError = true;
      this.errorMessage = "Please select Organization";
      return false;
    }

    if(this.selectedPeer == "0"){
      this.showError = true;
      this.errorMessage = "Please select Peer";
      return false;
    }

    if(this.selectedChannel == "0"){
      this.showError = true;
      this.errorMessage = "Please select Channel";
      return false;
    }

    if(this.selectedChaincode == "0"){
      this.showError = true;
      this.errorMessage = "Please select Chaincode";
      return false;
    }

    if(this.functionName.trim() == ""){
      this.showError = true;
      this.errorMessage = "Please enter Funtion Name";
      return false;
    }

    if(this.args.trim() == ""){
      this.showError = true;
      this.errorMessage = "Please enter Arguments";
      return false;
    }

    return true;
  }

  onClickRequest(){
    this.showLoading = true;
    this.showError = false;
    this.result =  "";

    if(this.validateInputs()) {
      if(this.selectedQueryType == "query"){
        this.consoleService.queryChaincode({
          networkId : this.networkid,
          orgId : this.selectedOrg,
          peer:this.selectedPeer,
          channelName: this.selectedChannel,
          chaincodeName : this.selectedChaincode,
          fcn:this.functionName,
          args : this.convertTextToArray(this.args)
        }).then((response: any)=>{
          this.showLoading = false;
          if(response.errorCode == 0){
            this.result =  response.msg
          } else {
            this.result =  response.msg
            this.showError = true;
            this.errorMessage =  response.msg;
          }
          
        })
        .catch((err)=>{
          this.showLoading = false;
          this.showError = true;
          this.errorMessage = "Invalid Arguments";
        })
      } else {
        this.consoleService.invokeChaincode({
          networkId : this.networkid,
          orgId : this.selectedOrg,
          peer: [this.selectedPeer],
          channelName: this.selectedChannel,
          chaincodeName : this.selectedChaincode,
          fcn:this.functionName,
          args : this.convertTextToArray(this.args)
        }).then((response: any)=>{
          this.showLoading = false;
          if(response.errorCode == 0){
            this.result =  response.msg
          } else {
            this.result =  response.msg
            this.showError = true;
            this.errorMessage =  response.msg;
          }
          
        })
        .catch((err)=>{
          this.showLoading = false;
          this.showError = true;
          this.errorMessage = "Invalid Arguments";
        })
      }
    } else {
      this.showLoading = false;

    }
  }



  ngOnInit() {
    this.networkid = this.route.snapshot.paramMap.get('networkid');
    console.log("ngOnInit :: APIPage :: networkid :: ",this.networkid);
    this.retrieveNetworkBasicDetails(this.networkid)
  }
}



