import { Component} from '@angular/core';
import { NewCotrustService } from '../../services/newconsole.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import 'rxjs/add/operator/switchMap';

@Component({
  selector: 'channelpage',
  templateUrl: './channelpage.component.html',
  styleUrls: ['./channelpage.component.css']
})


export class ChannelPage {

  channels = [];
  modalChaincodeInstallPeer = [];
  selectedChannelInInstalledChaincode = "";
  newChannelName = "";
  networkid

  selectedOrg = "";
  selectedOrgDesc = "-";
  showLoading = false;

  showError = false;
  showInfo = false;
  errorMessage = "";
  
  peerInstallChaincodeArray = [];
  peerCreateChannelArray = [];
  orgCreateChannelArray = [];
  peerJoinChannelArray = [];
  
  peerDetailsArray = [];
  orgDetailsArray = [];
  channelJoinRequestArray = [];
  channelJoinRequestDisplayArray = [];
  showChannelRequest = false;
  showJoinChannelLoading = false;

  showJoinChannelError = false;
  showJoinChannelInfo = false;
  joinChannelErrorMessage = "";  

  showNewChannelError = false;
  showNewChannelInfo = false;
  newChannelErrorMessage = "";  
  showNewChannelLoading = false;

  newChaincodeName = "";
  newChaincodeVersion = "";
  newChaincodeArguments = "";
  newChaincodeFileName = "";
  newChaincodeFileContent = "";

  smartcontractlist = [];
  smartContractID = "NA";

  constructor(private router: Router, private consoleService: NewCotrustService, private route: ActivatedRoute) {
    this.channels = [];

  }

  noChannelToDisplay(){
    if(this.channels && this.channels.length > 0){
      return false;
    } else {
      return true;
    }
  }

  onChangeChannelInInstallChaincode(){
    console.log("onChangeChannelInInstallChaincode() :: channels :: ", this.channels)
    for(let channel of this.channels){
      if(this.selectedChannelInInstalledChaincode == channel.channelName) {
        this.modalChaincodeInstallPeer = channel.peers;
      }
    }
  }

  retrieveAllSmartContract(networkid){
    this.smartcontractlist = [{smName : "NA", contractID : "NA"}]
    this.consoleService.retrieveAllSmartContract(networkid)
    .then((response: any)=>{
      console.log("retrieveAllSmartContract() :: response ::",response);
      for(let i=0; i < response.list.length; i++){
        this.smartcontractlist.push(response.list[i]);
      }
      
    })
    .catch((err) =>{
      this.showLoading = false;
      console.log("retrieveAllSmartContract() :: err ::",err);
      this.showInfo = false;
      this.showError = true;
      this.errorMessage = "Network Error";
    })
  }

  iAndiError = false;
  iAndiInfo = false;
  iAndiErrorMsg = "";
  iAndiProcessing = false;

  installAndInstantiateChaincode(){
    this.iAndiError = false;
    this.iAndiInfo = false;
    this.iAndiErrorMsg = "";
    
    console.log("onChangeChannelInInstallChaincode() :: selectedChannelInInstalledChaincode :: ", this.selectedChannelInInstalledChaincode,
                          ":: modalChaincodeInstallPeer :: ", this.modalChaincodeInstallPeer,
                          ":: newChaincodeName :: ", this.newChaincodeName,
                          ":: newChaincodeVersion :: ", this.newChaincodeVersion,
                          ":: newChaincodeArguments :: ", this.newChaincodeArguments,
                          ":: newChaincodeFileName :: ", this.newChaincodeFileName,
                          ":: newChaincodeFileContent :: ", this.newChaincodeFileContent);
    if(this.validateInstallAndInstantiateChaincode()){
      this.iAndiProcessing = true;

      if(this.smartContractID == "NA"){

        this.consoleService.installAndInstantiateChaincode({
          "networkId" : this.networkid,
          "orgId" : this.selectedOrg,
          "peers":this.modalChaincodeInstallPeer,
          "channelName": this.selectedChannelInInstalledChaincode,
          "chaincodeName" : this.newChaincodeName,
          "chaincodeVersion" : this.newChaincodeVersion,
          "args" : this.convertTextToArray(this.newChaincodeArguments),
          "filename" :this.newChaincodeFileName,
          "filecontent" :  this.newChaincodeFileContent
        })
        .then((response: any)=>{
          this.iAndiProcessing = false;
          if(response && response.errorCode == 0){
            this.iAndiError = false;
            this.iAndiInfo = true;
            this.iAndiErrorMsg = response.msg;

            this.newChaincodeName = "";
            this.newChaincodeVersion = "";
            this.newChaincodeArguments = "";
            this.newChaincodeFileName = "";
            this.newChaincodeFileContent = "";
            
            this.onChangeOrganization();
          } else if(response && response.errorCode != 0){
            this.iAndiError = true;
            this.iAndiInfo = false;
            this.iAndiErrorMsg = response.msg + "   "+ response.err;
          } else {
            this.iAndiError = true;
            this.iAndiInfo = false;
            this.iAndiErrorMsg = "Server Error";
          }
        })
        .catch((err)=>{
          this.iAndiProcessing = false;
          this.iAndiError = true;
          this.iAndiInfo = false;
          this.iAndiErrorMsg = "Network Error";
        })
      } else {
        this.consoleService.installAndInstantiateChaincodeDynamicContract({
          "networkId" : this.networkid,
          "orgId" : this.selectedOrg,
          "peers":this.modalChaincodeInstallPeer,
          "channelName": this.selectedChannelInInstalledChaincode,
          "chaincodeName" : this.newChaincodeName,
          "chaincodeVersion" : this.newChaincodeVersion,
          "args" : this.convertTextToArray(this.newChaincodeArguments),
          "smartContractId" :this.smartContractID
        })
        .then((response: any)=>{
          this.iAndiProcessing = false;
          if(response && response.errorCode == 0){
            this.iAndiError = false;
            this.iAndiInfo = true;
            this.iAndiErrorMsg = response.msg;

            this.newChaincodeName = "";
            this.newChaincodeVersion = "";
            this.newChaincodeArguments = "";
            this.newChaincodeFileName = "";
            this.newChaincodeFileContent = "";
            
            this.onChangeOrganization();
          } else if(response && response.errorCode != 0){
            this.iAndiError = true;
            this.iAndiInfo = false;
            this.iAndiErrorMsg = response.msg + "   "+ response.err;
          } else {
            this.iAndiError = true;
            this.iAndiInfo = false;
            this.iAndiErrorMsg = "Server Error";
          }
        })
        .catch((err)=>{
          this.iAndiProcessing = false;
          this.iAndiError = true;
          this.iAndiInfo = false;
          this.iAndiErrorMsg = "Network Error";
        })
      }
    }
  }

  convertTextToArray(arrayString){
    let jsonString = '{ "value" : ['+arrayString+']}';
    console.log("convertTextToArray :: jsonString :: ", jsonString);
    return JSON.parse(jsonString).value;
  }

  validateInstallAndInstantiateChaincode(){
    this.iAndiError = false;
    this.iAndiInfo = false;
    this.iAndiErrorMsg = "";

    if(this.selectedChannelInInstalledChaincode == ""){
      this.iAndiError = true;
      this.iAndiInfo = false;
      this.iAndiErrorMsg = "Please select Channel ";
      return false;
    }

    if(this.newChaincodeName == ""){
      this.iAndiError = true;
      this.iAndiInfo = false;
      this.iAndiErrorMsg = "Please enter Chaincode Name ";
      return false;
    }

    if(this.newChaincodeVersion == ""){
      this.iAndiError = true;
      this.iAndiInfo = false;
      this.iAndiErrorMsg = "Please enter Chaincode Version ";
      return false;
    }
    if(this.smartContractID == "NA"){
      if(this.newChaincodeFileName == ""){
        this.iAndiError = true;
        this.iAndiInfo = false;
        this.iAndiErrorMsg = "Please Select Chaincode File ";
        return false;
      }
    }

    return true;
  }


  fileChange(event) {
    let reader = new FileReader();
    if(event.target.files && event.target.files.length > 0) {
      let file = event.target.files[0];
      console.log("file :: ",file)
      reader.readAsDataURL(file);
      reader.onload = () => {
        console.log("reader :: ",reader)
        console.log("reader Data :: ",reader.result.split(',')[1])
        this.newChaincodeFileName = file.name;
        this.newChaincodeFileContent = reader.result.split(',')[1];
      };
    }
  }

  retrieveNetworkBasicDetails(networkid){
    this.showLoading = true;
    this.consoleService.retrieveNetworkBasicDetails(networkid)
      .then((response: any) => {
        this.showLoading = false;
        this.orgDetailsArray = [];
        this.channelJoinRequestArray = [];
        
        if(response.channelJoinRequest){
          this.channelJoinRequestArray =response.channelJoinRequest;
        }
        
        for(let networdetails of response.networkbasicdetail) {
          let peers = [];
          for(let tmpPeer of networdetails.peers) {
            peers.push(tmpPeer);
            
          }
          this.orgDetailsArray.push({
            orgName : networdetails.orgName,
            orgIdentifier : networdetails.orgIdentifier,
            peers : peers
          })
        }

        if(this.orgDetailsArray.length > 0){
          this.selectedOrg = this.orgDetailsArray[0].orgIdentifier;
          this.selectedOrgDesc = this.orgDetailsArray[0].orgName;
          this.peerDetailsArray = this.orgDetailsArray[0].peers;
          this.peerInstallChaincodeArray = [];
          this.peerCreateChannelArray = [];
          for(let tmpPeer of this.peerDetailsArray){
            console.log("peerDetailsArray :: tmpPeer :: ", tmpPeer);
            this.retrieveChannels(this.selectedOrg, tmpPeer, this.networkid);
          }
        }

        this.channelJoinRequestDisplayArray = [];
        this.showChannelRequest = false;
        for(let tmpJoinRequest of this.channelJoinRequestArray){
          if(tmpJoinRequest.orgId == this.selectedOrg){
            this.channelJoinRequestDisplayArray.push(tmpJoinRequest.channelName);
          }
        }
        
        if(this.channelJoinRequestDisplayArray.length > 0){
          this.showChannelRequest = true;
        }
        console.log("Dashboard :: selectedOrg :: ",this.selectedOrg, " :: orgDetailsArray :: ", this.orgDetailsArray);

      })
      .catch((err) =>{
        this.showLoading = false;
        console.log("retrieveNetworkBasicDetails() :: err ::",err);
        this.showInfo = false;
        this.showError = true;
        this.errorMessage = "Network Error";
      })
  }


  retrieveJoinChannelNotification(networkid){
    this.consoleService.retrieveNetworkBasicDetails(networkid)
      .then((response: any) => {
        this.channelJoinRequestArray = [];
        
        if(response.channelJoinRequest){
          this.channelJoinRequestArray =response.channelJoinRequest;
        }
        this.channelJoinRequestDisplayArray = [];
        this.showChannelRequest = false;
        for(let tmpJoinRequest of this.channelJoinRequestArray){
          if(tmpJoinRequest.orgId == this.selectedOrg){
            this.channelJoinRequestDisplayArray.push(tmpJoinRequest.channelName);
          }
        }
        
        if(this.channelJoinRequestDisplayArray.length > 0){
          this.showChannelRequest = true;
        }
        console.log("Dashboard :: selectedOrg :: ",this.selectedOrg, " :: channelJoinRequestDisplayArray :: ", this.channelJoinRequestDisplayArray);

      })
      .catch((err) =>{
        this.showLoading = false;
        console.log("retrieveJoinChannelNotification() :: err ::",err);
        this.showInfo = false;
        this.showError = true;
        this.errorMessage = "Network Error";
      })
  }

  selectedJoinChannelName = "";
  onClickJoinChannel(channelname){
    console.log("onClickJoinChannel() :: selectedOrg ::",this.selectedOrg, " :: channelname :: ", channelname);
    this.selectedJoinChannelName = channelname;
  }

  
  requestJoinChannel(){
    if(this.peerJoinChannelArray.length == 0){
      this.showJoinChannelError = true;
      this.showJoinChannelInfo = false;
      this.joinChannelErrorMessage = "Please select minimum one peer ";  
    } else {
      this.showJoinChannelError = false;
      this.showJoinChannelInfo = false;
      this.showJoinChannelLoading = true;
      this.consoleService.joinChannel(this.networkid,this.selectedOrg, this.peerJoinChannelArray, this.selectedJoinChannelName)
        .then((response: any)=>{
          this.showJoinChannelLoading = false;
          if(response && response.errorCode == 0){
            this.showJoinChannelError = false;
            this.showJoinChannelInfo = true;
            this.joinChannelErrorMessage = response.msg;  
            this.onChangeOrganization();
          } else if(response && response.errorCode != 0){
            this.showJoinChannelError = true;
            this.showJoinChannelInfo = false;
            this.joinChannelErrorMessage = response.msg;  
          } else {
            this.showJoinChannelError = true;
            this.showJoinChannelInfo = false;
            this.joinChannelErrorMessage = "Server Error"; 
          }
        })
        .catch((err)=>{
          this.showJoinChannelLoading = false;
          console.log("requestJoinChannel() :: err ::",err);
          this.showJoinChannelError = true;
          this.showJoinChannelInfo = false;
          this.joinChannelErrorMessage = "Network Error"; 
        })
    }
  }
  onClickPeerJoinChannel(peer){
    console.log("onClickPeerJoinChannel :: peer :: ", peer);
    let hasPeer = false;
    let newpeerJoinChannelArray = [];
    for(let tmpPeer of this.peerJoinChannelArray){
      if(tmpPeer == peer){
        hasPeer = true;
      } else {
        newpeerJoinChannelArray.push(tmpPeer);
      }
    }
    if(hasPeer){
      this.peerJoinChannelArray = newpeerJoinChannelArray;
    } else {
      this.peerJoinChannelArray.push(peer);
    }

    console.log("onClickPeerJoinChannel :: peerJoinChannelArray :: ", this.peerJoinChannelArray);
  }
  

  retrieveChannels(selectedOrg, peerName, networkid){
    this.showLoading = true;
    this.showInfo = false;
    this.showError = false;
    this.consoleService.retrieveOrgDashChannelDetails(networkid,selectedOrg,peerName)
    .then((response: any)=>{
      this.showLoading = false;
      console.log("retrieveChannels() :: response ::",response);
      if(this.networkid == networkid && this.selectedOrg == selectedOrg){
        if(response.channelNames){
          for(let channelName of response.channelNames){
            let channelAdded = false;
            for(let index in this.channels){
              if(this.channels[index].channelName == channelName.channelId){
                channelAdded = true;
                this.channels[index].peers.push(peerName);
                break;
              }
            }
            if(!channelAdded){
              this.channels.push({
                channelName : channelName.channelId,
                peers:[peerName],
                chanicodes:[]
              })
            }
            this.retrieveInstantiatedChaincode(networkid, selectedOrg, peerName, channelName.channelId)
          }
        }        
      }
      console.log("retrieveChannels() :: channels ::",this.channels);
    })
    .catch((err) =>{
      this.showLoading = false;
      console.log("retrieveChannelCount() :: err ::",err);
      this.showInfo = false;
      this.showError = true;
      this.errorMessage = "Network Error";
    })
  }

  retrieveInstantiatedChaincode(networkid, selectedOrg, peerName, channelName){
    this.consoleService.retrieveOrgInstantiatedDashChaincodeDetails(networkid,selectedOrg,peerName,channelName)
    .then((response: any)=>{
      if(this.networkid == networkid && this.selectedOrg == selectedOrg){
        if(response.list && response.list.chaincodes){
          for(let index in this.channels){
            if(this.channels[index].channelName == channelName){
              for(let newChaincode of response.list.chaincodes){
                let alreadyExists = false;
                for(let chaincode of this.channels[index].chanicodes){
                  if(newChaincode.name == chaincode.chaincodeName && newChaincode.version == chaincode.chaincodeVersion){
                    alreadyExists = true;
                    break;
                  }
                }
                if(!alreadyExists){
                  this.channels[index].chanicodes.push({
                    chaincodeName : newChaincode.name,
                    chaincodeVersion : newChaincode.version,
                    chaincodePath : newChaincode.path
                  })
                }
              }              
            }
          }
        }
      }
    })
    .catch((err) =>{
      this.showLoading = false;
      console.log("retrieveInstantiatedChaincode() :: err ::",err);
      this.showInfo = false;
      this.showError = true;
      this.errorMessage = "Network Error";
    })
  }

  onChangeOrganization(){
    this.orgCreateChannelArray = [];
    for(let orgDetails of this.orgDetailsArray) {
      if(orgDetails.orgIdentifier == this.selectedOrg){
        this.selectedOrg = orgDetails.orgIdentifier;
        this.selectedOrgDesc = orgDetails.orgName;
        this.peerDetailsArray = orgDetails.peers;
        this.channels = [];
        this.peerInstallChaincodeArray = [];
        for(let tmpPeer of this.peerDetailsArray){
          console.log("onChangeOrganization :: tmpPeer :: ", tmpPeer);
          this.retrieveChannels(this.selectedOrg, tmpPeer, this.networkid);
        }
      }
    }
    
    this.channelJoinRequestDisplayArray = [];
    this.showChannelRequest = false;
    this.retrieveJoinChannelNotification(this.networkid);
  }

  createChannel(){
    console.log("createChannel :: peerCreateChannelArray :: ", this.peerCreateChannelArray);
    this.showNewChannelLoading = false;
    if(this.newChannelName.trim() == ""){
      this.showNewChannelError = true;
      this.showNewChannelInfo = false;
      this.newChannelErrorMessage = "Invalid Channel Name";  
    } else if(this.peerCreateChannelArray.length == 0){
      this.showNewChannelError = true;
      this.showNewChannelInfo = false;
      this.newChannelErrorMessage = "Please select minimum one peer ";  
    } else {
      this.showNewChannelError = false;
      this.showNewChannelInfo = true;
      this.newChannelErrorMessage = "Creating Channel";  
      this.showNewChannelLoading = true;
      this.consoleService.createChannel(this.networkid,this.selectedOrg, this.peerCreateChannelArray,this.orgCreateChannelArray, this.newChannelName)
      .then((response: any)=>{
        this.showNewChannelLoading = false;
        if(response && response.errorCode == 0){
          this.showNewChannelError = false;
          this.showNewChannelInfo = true;
          this.newChannelErrorMessage = response.msg; 
          this.newChannelName = ""; 
          this.onChangeOrganization();
        } else if(response && response.errorCode != 0){
          this.showNewChannelError = true;
          this.showNewChannelInfo = false;
          this.newChannelErrorMessage = response.msg;  
        } else {
          this.showNewChannelError = true;
          this.showNewChannelInfo = false;
          this.newChannelErrorMessage = "Server Error";  
        }
      })
      .catch((err)=>{
        this.showNewChannelLoading = false;
        console.log("createChannel() :: err ::",err);
        this.showNewChannelInfo = false;
        this.showNewChannelError = true;
        this.newChannelErrorMessage = "Network Error";
      })
    }
    
  }


  onClickPeerChannel(peer){
    console.log("onClickPeerChannel :: peer :: ", peer);
    let hasPeer = false;
    let newpeerCreateChannelArray = [];
    for(let tmpPeer of this.peerCreateChannelArray){
      if(tmpPeer == peer){
        hasPeer = true;
      } else {
        newpeerCreateChannelArray.push(tmpPeer);
      }
    }
    if(hasPeer){
      this.peerCreateChannelArray = newpeerCreateChannelArray;
    } else {
      this.peerCreateChannelArray.push(peer);
    }

    console.log("onClickPeerChannel :: peerCreateChannelArray :: ", this.peerCreateChannelArray);
    
  }

  
  onClickOrgChannel(orgid){
    console.log("onClickOrgChannel :: orgid :: ", orgid);
    let hasOrg = false;
    let newOrgCreateChannelArray = [];
    for(let tmpOrgId of this.orgCreateChannelArray){
      if(tmpOrgId == orgid){
        hasOrg = true;
      } else {
        newOrgCreateChannelArray.push(tmpOrgId);
      }
    }
    if(hasOrg){
      this.orgCreateChannelArray = newOrgCreateChannelArray;
    } else {
      this.orgCreateChannelArray.push(orgid);
    }

    console.log("onClickOrgChannel :: orgCreateChannelArray :: ", this.orgCreateChannelArray);
    
  }




  ngOnInit() {
    this.networkid = this.route.snapshot.paramMap.get('networkid');
    console.log("ngOnInit :: ChannelPage :: networkid :: ",this.networkid);
    this.retrieveNetworkBasicDetails(this.networkid);
    this.retrieveAllSmartContract(this.networkid)
  }
}



