import { Component, Input} from '@angular/core';
import { NewCotrustService } from '../../services/newconsole.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import 'rxjs/add/operator/switchMap';

@Component({
  selector: 'dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})


export class Dashboard {

  @Input('networkid')
  networkid

  peerDetailsArray = [];
  orgDetailsArray = [];
  selectedOrg = "";
  selectedOrgDesc = "-";
  showLoading = false;

  showError = false;
  showInfo = false;
  errorMessage = "";
  
  constructor(private router: Router, private consoleService: NewCotrustService, private route: ActivatedRoute) {
    console.log("Dashboard :: networkid :: ",this.networkid);
  }

  

  retrieveNetworkBasicDetails(networkid){
    this.showLoading = true;
    this.consoleService.retrieveNetworkBasicDetails(networkid)
      .then((response: any) => {
        this.showLoading = false;
        this.orgDetailsArray = [];
        
        
        for(let networdetails of response.networkbasicdetail) {
          let peers = [];
          for(let tmpPeer of networdetails.peers) {
            let peer: any = this.defaultPeerConfig();
            peer.peerName = tmpPeer;
            peer.channelCount="-";
            peer.installedChaincode="-";
            peers.push(peer);
            
          }
          this.orgDetailsArray.push({
            orgName : networdetails.orgName,
            orgIdentifier : networdetails.orgIdentifier,
            peers : peers
          })
        }

        if(this.orgDetailsArray.length > 0){
          this.selectedOrg = this.orgDetailsArray[0].orgIdentifier;
          this.selectedOrgDesc = this.orgDetailsArray[0].orgName;
          this.peerDetailsArray = this.orgDetailsArray[0].peers;
          for(let index in this.peerDetailsArray){
            console.log("peerDetailsArray :: index :: ", index, this.peerDetailsArray[index].peerName);
            this.retrieveChaincodeCount(index, this.selectedOrg, this.peerDetailsArray[index].peerName, this.networkid);
            this.retrieveChannelCount(index, this.selectedOrg, this.peerDetailsArray[index].peerName, this.networkid);
          }
        }
        
        console.log("Dashboard :: selectedOrg :: ",this.selectedOrg, " :: orgDetailsArray :: ", this.orgDetailsArray);

      })
      .catch((err) =>{
        this.showLoading = false;
        console.log("retrieveNetworkBasicDetails() :: err ::",err);
        this.showError = true;
        this.errorMessage = "Network Error";
      })
  }

  retrieveChaincodeCount(index, selectedOrg, peerName, networkid){
    this.consoleService.retrieveOrgDashChaincodeDetails(networkid,selectedOrg,peerName)
    .then((response: any)=>{
      console.log("retrieveChaincodeCount() :: response ::",response);

      if(response && response.errorCode == 0 && this.networkid == networkid && this.selectedOrg == selectedOrg){
        this.peerDetailsArray[index].installedChaincode= response.list.chaincodes.length;
      } else if(response && response.errorCode && response.errorCode != 0) {
        this.showError = true;
        this.errorMessage = response.msg;
      }
    })
    .catch((err) =>{
      this.showLoading = false;
      console.log("retrieveChaincodeCount() :: err ::",err);
      this.showError = true;
      this.errorMessage = "Network Error";
    })
  }


  onClickAddPeer(){
    this.showLoading = true;
    this.showError = false;
    this.errorMessage = "";
    this.consoleService.createPeer(this.networkid, this.selectedOrg)
      .then((response: any) => {
        setTimeout(()=>{
          this.retrieveNetworkBasicDetails(this.networkid);
        },20000);
        
      })
      .catch((err) =>{
        this.showLoading = false;
        console.log("onClickAddPeer() :: err ::",err);
        this.showError = true;
        this.errorMessage = "Network Error";
      })
  }

  retrieveChannelCount(index, selectedOrg, peerName, networkid){
    this.consoleService.retrieveOrgDashChannelDetails(networkid,selectedOrg,peerName)
    .then((response: any)=>{
      console.log("retrieveChannelCount() :: response ::",response);
      if(this.networkid == networkid && this.selectedOrg == selectedOrg){
        if(response.channelNames){
          this.peerDetailsArray[index].channelCount= response.channelNames.length;
        }        
      }
    })
    .catch((err) =>{
      this.showLoading = false;
      console.log("retrieveChannelCount() :: err ::",err);
      this.showError = true;
      this.errorMessage = "Network Error";
    })
  }

  defaultPeerConfig(){
    let peer: any = {};
    peer.peerName = "peer";
    peer.channelCount="-";
    peer.installedChaincode="-";
    peer.status="primary";

    return peer;
  }

  onChangeOrganization(){
    console.log("Dashboard :: networkid :: ",this.networkid," :: selectedOrg :: ", this.selectedOrg);
    for(let orgDetails of this.orgDetailsArray) {
      if(orgDetails.orgIdentifier == this.selectedOrg){
        this.selectedOrg = orgDetails.orgIdentifier;
        this.selectedOrgDesc = orgDetails.orgName;
        this.peerDetailsArray = orgDetails.peers;
        for(let index in this.peerDetailsArray){
          console.log("onChangeOrganization :: index :: ", index, this.peerDetailsArray[index].peerName);
          this.retrieveChaincodeCount(index, this.selectedOrg, this.peerDetailsArray[index].peerName, this.networkid);
          this.retrieveChannelCount(index, this.selectedOrg, this.peerDetailsArray[index].peerName, this.networkid);
        }
      }
    }
  }

  selectedOrgIndex(){
    let selectedOrdIndex:number = 0;
    for(let orgDetails of this.orgDetailsArray) {
      selectedOrdIndex++;
      if(orgDetails.orgIdentifier == this.selectedOrg){
        return selectedOrdIndex;
      }
    }
  }

  nextPeerNumber(){
    return this.peerDetailsArray.length;
  }

  ngOnInit() {
    this.networkid = this.route.snapshot.paramMap.get('networkid');
    console.log("ngOnInit :: Dashboard :: networkid :: ",this.networkid);
    this.retrieveNetworkBasicDetails(this.networkid)
  }

}



