import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { LocalStorageService } from 'angular-2-local-storage';
import { Headers, Http } from '@angular/http';
import { Constants } from '../pojo/constanst'

@Injectable()
export class NewCotrustService {
    constructor(private router: Router, public http: Http, private localStorageService: LocalStorageService) {

    }

    /**
     * 
     *  Sign Up Service
     * 
     * @param userData 
     */
    signUp(userData){
        let url = Constants.SIGNUP_URL;
        return this.sendPOSTRequest(url, userData);
    }

    retrieveAllUnapprovedUser(){
        let url = Constants.ALL_UNAPPROVED_USERS;
        return this.sendGETRequest(url);
    }

    approveUser(postData){
        let url = Constants.APPROVE_USERS;
        return this.sendPOSTRequest(url,postData);
    }
    
    confirmMailId(confirmMailToken){
        let url = Constants.CONFIRMMAILID_URL;
        return this.sendPOSTRequest(url,{ mailtoken : confirmMailToken});
    }

    loginUserName(){
        let userName = "";
        userName = JSON.stringify(this.localStorageService.get(Constants.LOGIN_USER_NAME)).replace(/"/g,"");
        return userName;
    }

    loginUserEmail(){
        let userName = "";
        userName = JSON.stringify(this.localStorageService.get(Constants.LOGIN_USER_EMAIL));//.replace(/"/g,"");
        return userName;
    }


    /**
     * 
     *  Login Service
     * 
     * @param loginData 
     */
    login(loginData){
        let url = Constants.LOGIN_URL;
        return new Promise((resolve, reject) => {
            this.sendPOSTRequest(url, loginData)
                .then((response: any) => {
                    if(response.errorCode == 0) {
                        this.localStorageService.set(Constants.TOKEN, response.token);
                        this.localStorageService.set(Constants.LOGIN_USER_NAME, response.name);
                        this.localStorageService.set(Constants.LOGIN_USER_EMAIL, loginData.email);
                    }
                    return resolve(response);
                }).catch(err => {
                    return reject(err);
                })
        });
    }

    logout(){
        this.localStorageService.remove(Constants.TOKEN, "");
        this.localStorageService.remove(Constants.LOGIN_USER_EMAIL, "");
        this.localStorageService.remove(Constants.LOGIN_USER_NAME, "");
        this.sendGETRequest(Constants.LOGOUT_URL);
        this.router.navigate(['/login']);  
    }
    
    /**
     * #############################################################
     * 
     *               FORGOT PASSWORD
     * 
     * #############################################################
     * 
     */

    sendOTPToMailID(postdata){
        let url = Constants.SENT_OTP_TO_MAILID;
        return this.sendPOSTRequest(url, postdata);
    }

    verifyOTP(postdata){
        let url = Constants.VERIFY_OTP;
        return new Promise((resolve, reject) => {
            this.sendPOSTRequest(url, postdata)
            .then((response: any) => {
                if(response.errorCode == 0) {
                    this.localStorageService.set(Constants.TOKEN, response.token);
                    this.localStorageService.set(Constants.LOGIN_USER_NAME, response.name);
                }
                return resolve(response);
            }).catch(err => {
                return reject(err);
            })
        });
    }

    /**
     * #######################################
     *          Private key details
     * #######################################
     */

    retrievePrivateKeyDetailsForUser(userEmail){
        let url = Constants.RETRIEVE_PRIVATKEY_DATA_FOR_USER + '/'+userEmail;
        return this.sendGETRequest(url);
    }

    uploadPrivateKey(postData) {
        let url = Constants.UPLOAD_PRIVATKEY;
        return this.sendPOSTRequest(url, postData);
    }

    checkPrivateKeyPresent(folderName) {
        let url = Constants.CHECK_FILE_IS_PRESENT + '/'+folderName;
        return this.sendGETRequest(url);
    }

    /**
     * #######################################
     *          Network Dashboard
     * #######################################
     */

    retrieveAllnetworkForUser(){
        let url = Constants.RETRIEVE_NETWORK_DATA_FOR_USER;
        return this.sendGETRequest(url);
    }

    /**
     * ####################################
     * 
     *      Organization Dashboard
     * 
     * ####################################
     */
    
    retrieveNetworkBasicDetails(networkid){
        let url = Constants.RETRIEVE_NETWORK_BASIC_DETAILS+networkid;
        return this.sendGETRequest(url);
    }

    retrieveNetworkDetails(networkid){
        let url = Constants.RETRIEVE_NETWORK_DETAILS+networkid;
        return this.sendGETRequest(url);
    }


    retrieveOrgDashChannelDetails(networkid, orgid, peername){
        let url = Constants.RETRIEVE_ORG_CHANNNEL_NAMES+networkid+"/"+orgid+"/"+peername;
        return this.sendGETRequest(url);
    }

    retrieveOrgDashChaincodeDetails(networkid, orgid, peername){
        let url = Constants.RETRIEVE_ORG_CHAINCODE_DETAILS+networkid+"/"+orgid+"/"+peername;
        return this.sendGETRequest(url);
    }

    retrieveOrgInstantiatedDashChaincodeDetails(networkid, orgid, peername, channelName){
        let url = Constants.RETRIEVE_ORG_INSTANTIATED_CHAINCODE_DETAILS+networkid+"/"+orgid+"/"+peername+"/"+channelName;
        return this.sendGETRequest(url);
    }

    retrieveAllCertificates(networkid, orgid){
        let url = Constants.RETRIEVE_ALL_CERTIFICATES+networkid+"/"+orgid;
        return this.sendGETRequest(url);
    }
    
    deleteNetwork(networkid){
        let url = Constants.DELETE_NETWORK+networkid;
        return this.sendDeleteRequest(url);
    }

    resetPassword(newpassword){
        let url = Constants.RESET_PASSWORD;
        return this.sendPOSTRequest(url, {
            newpassword : newpassword
        })
    }

    /**
     * #############################################################
     * 
     *                      Create Channel
     * 
     * #############################################################
     * 
     */

    createChannel(networkid, orgid, peers, orgIds, channelName){
        let url = Constants.CREATE_CHANNEL;
        return this.sendPOSTRequest(url, {
            networkId:networkid,
            orgId:orgid,
            orgIdArray : orgIds,
            channelName : channelName,
            peers : peers
        });
    }

    /**
     * #############################################################
     * 
     *                      Join Channel
     * 
     * #############################################################
     * 
     */

    joinChannel(networkid, orgid, peers, channelName){
        let url = Constants.JOIN_CHANNEL;
        return this.sendPOSTRequest(url, {
            networkId:networkid,
            orgId:orgid,
            channelName : channelName,
            peers : peers
        });
    }

    /**
     * #############################################################
     * 
     *               Install and Instantiate Chaincode
     * 
     * #############################################################
     * 
     */

    installAndInstantiateChaincode(postData){
        let url = Constants.INSTALL_AND_INSTANTIATE_CHAINCODE;
        return this.sendPOSTRequest(url, postData);
    }

    installAndInstantiateChaincodeDynamicContract(postData){
        let url = Constants.INSTALL_AND_INSTANTIATE_CHAINCODE_DYNAMIC_CONTRACT;
        return this.sendPOSTRequest(url, postData);
    }
    
    /**
     * #############################################################
     * 
     *               Query Chaincode
     * 
     * #############################################################
     * 
     */

    queryChaincode(postData){
        let url = Constants.QUERY_CHAINCODE;
        return this.sendPOSTRequest(url, postData);
    }

    /**
     * #############################################################
     * 
     *               Invoke Chaincode
     * 
     * #############################################################
     * 
     */

    invokeChaincode(postData){
        let url = Constants.INVOKE_CHAINCODE;
        return this.sendPOSTRequest(url, postData);
    }

    /**
     * #############################################################
     * 
     *               Block Height
     * 
     * #############################################################
     * 
     */

    blockHeight(networkid, orgid, peer, channelname){
        let url = Constants.BLOCK_HEIGHT_LIST_URL+networkid+"/"+orgid+"/"+peer+"/"+channelname;
        return this.sendGETRequest(url);
    }

    
    blockDetails(networkid, orgid, peer, channelname,blocknumber){
        let url = Constants.BLOCK_BY_NUMBER_URL+networkid+"/"+orgid+"/"+peer+"/"+channelname+"/"+blocknumber;
        return this.sendGETRequest(url);
    }

    /**
     * #############################################################
     * 
     *                      Create User
     * 
     * #############################################################
     * 
     */

    createUserSecret(postData: any){
        let url = Constants.CREATE_USER_SECRET;
        return this.sendPOSTRequest(url,postData);
    }

    createUserCertificates(postData: any){
        let url = Constants.CREATE_USER_CERTIFICATE;
        return this.sendPOSTRequest(url,postData);
    }
    
    /**
     * #############################################################
     * 
     *                      Create Peer
     * 
     * #############################################################
     * 
     */
    createPeer(networkid, orgid){
        let url = Constants.CREATE_PEER+networkid+"/"+orgid;
        return this.sendPOSTRequest(url,{});
    }

    /**
     * #############################################################
     * 
     *                      Create Type
     * 
     * #############################################################
     * 
     */
    createType(typeJSONData){
        let url = Constants.CREATE_TYPE;
        return this.sendPOSTRequest(url,typeJSONData);
    }

    retrieveAllType(networkid){
        let url = Constants.ALL_TYPE+networkid;
        return this.sendGETRequest(url);
    }

    retrieveTypeByID(networkid, typeid){
        let url = Constants.ALL_TYPE+networkid+"/"+typeid;
        return this.sendGETRequest(url);
    }

    createSmartContract(networkid, smartcontractdata){
        let url = Constants.CREATE_SMARTCONTRACT+networkid;
        return this.sendPOSTRequest(url, smartcontractdata);
    }

    retrieveAllSmartContract(networkid){
        let url = Constants.CREATE_SMARTCONTRACT+networkid;
        return this.sendGETRequest(url);
    }

    retrieveSmartContract(networkid, smartcontractid){
        let url = Constants.CREATE_SMARTCONTRACT+networkid+"/"+smartcontractid;
        return this.sendGETRequest(url);
    }
    /**
     * #############################################################
     * 
     *                      UTILITIES
     * 
     * #############################################################
     * 
     */



    sendPOSTRequest(url, postData){
      
        return new Promise((resolve, reject) => {
            
          var  headers:any = new Headers({ 'Content-Type': 'application/json', 'token': this.localStorageService.get(Constants.TOKEN) });
          console.log("sendPOSTRequest :: ", url, headers, postData);
          return this.http.post(url,JSON.stringify(postData), { headers: headers })
              .toPromise()
              .then(data => {
                console.log("sendPOSTRequest :: ", url, headers, postData," :: data :: ", data);
                var response = data.json();
                this.invalidTokenCheck(response);
                return resolve(response);
              })
              .catch(err => {
                console.log("sendPOSTRequest :: ", url, headers, postData," :: err :: ", err);
                  return reject(err);
              })
            
        });
    }

    sendDeleteRequest(url){
      
        return new Promise((resolve, reject) => {
            
          var  headers:any = new Headers({ 'Content-Type': 'application/json', 'token': this.localStorageService.get(Constants.TOKEN) });
          console.log("sendDeleteRequest :: ", url, headers);
          return this.http.delete(url,{ headers: headers })
              .toPromise()
              .then(data => {
                console.log("sendPOSTRequest :: ", url, headers," :: data :: ", data);
                var response = data.json();
                this.invalidTokenCheck(response);
                return resolve(response);
              })
              .catch(err => {
                console.log("sendPOSTRequest :: ", url, headers," :: err :: ", err);
                  return reject(err);
              })
            
        });
    }

    sendGETRequest(url){
        return new Promise((resolve, reject) => {
            
          var  headers:any = new Headers({ 'Content-Type': 'application/json', 'token': this.localStorageService.get(Constants.TOKEN) });
          console.log("sendGETRequest :: ", url, headers);
          return this.http.get(url, { headers: headers })
              .toPromise()
              .then(data => {
                console.log("sendGETRequest :: ", url, headers," :: data :: ", data);
                var response = data.json();
                this.invalidTokenCheck(response);
                return resolve(data.json());
              })
              .catch(err => {
                console.log("sendGETRequest :: ", url, headers," :: err :: ", err);
                return reject(err);
              })
            
        });
    }

    invalidTokenCheck(response:any){
        if(response.errorCode == 609){
            console.log("===== Redirecting to Login ========")
            if(this.localStorageService.get(Constants.TOKEN) && this.localStorageService.get(Constants.TOKEN) != "") {
                this.logout();
            }
            return false;
        }
        return true;
    }
}



