import { Component} from '@angular/core';
import { NewCotrustService } from '../../services/newconsole.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import 'rxjs/add/operator/switchMap';

@Component({
  selector: 'console-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})


export class SettingsPage {

  configuration = {};
  networkid

  showLoading = false;
  showError = false;
  errorMessage = "";

  selectedOrg = "";
  orgConfigArray = [];

  constructor(private router: Router, private consoleService: NewCotrustService, private route: ActivatedRoute) {
    console.log("Dashboard :: Constructor Started ");
    this.configuration = {
        "orderer":{
            "url":"",
            "serverhostname":"",
            "tlscacerts":""
        },
        "peers": [
            {
                "name": "peer1",
                "requests":"",
                "events":"",
                "serverhostname":"",
                "tlscacerts":""
            },
        
            {
                "name": "peer2",
                "requests":"",
                "events":"",
                "serverhostname":"",
                "tlscacerts":""
            }
        ],
        "admin":{
            "username":"",
            "password":"",
            "key":"",
            "cert":""
        },
        "mspid":"",
        "ca":"",
        
        "cloudantaccount" : "",
        "cloudantpassword" : "",
        "cloudantdb" : ""
    };
    console.log("Dashboard :: Constructor Ends ");
  }

  retrieveNetworkDetails(networkid){
    this.showLoading = true;
    this.consoleService.retrieveNetworkDetails(networkid)
      .then((response: any) => {
        console.log("Dashboard :: retrieveNetworkDetails :: ",response);
        this.showLoading = false;
        if(response.errorCode == 0){
            console.log("Dashboard :: Valid response :: ",response);
            this.orgConfigArray = response.networkdetail;
            console.log("Dashboard :: this.orgConfigArray :: ",this.orgConfigArray);
            if(this.orgConfigArray && this.orgConfigArray.length > 0){
                console.log("Dashboard :: Assigning default value ");
                this.selectedOrg = this.orgConfigArray[0].orgIdentifier;
                this.configuration = this.orgConfigArray[0];
            }
            
        } else {
            this.showError = true;
            this.errorMessage = "No COnfig Available";
        }
        

      })
      .catch((err) =>{
        this.showLoading = false;
        console.log("retrieveNetworkBasicDetails() :: err ::",err);
        this.showError = true;
        this.errorMessage = "Network Error";
      })
  }

  onChangeOrganization(){
    console.log("onChangeOrganization() :: selectedOrg ::",this.selectedOrg);
    for(let org of this.orgConfigArray){
        if(org.orgIdentifier == this.selectedOrg){
            this.configuration = org;
            break;
        }
    }
  }


  ngOnInit() {
    this.networkid = this.route.snapshot.paramMap.get('networkid');
    console.log("ngOnInit :: SettingsPage :: networkid :: ",this.networkid);
    this.retrieveNetworkDetails(this.networkid)
  }

}



